
body {
  background : #f7f9fc;
}

.white-box {
	background: white;
	padding: 20px;
	border-radius: 5px;
	box-shadow: 0.5px 0.5px 2px 1px #eee;
}

.pill {
	display: inline-block;
	border: 1px solid lightgray;
	border-radius: 100px;
	padding: 3px 12px 5px 12px;
	margin-right: 2px;
	margin-bottom: 5px;
}

a {
	text-decoration: none;
}


@media screen and (min-width : 1024px) {
	.className {
		margin : '20 10 10 10'
	}
}

@media screen and (max-width : 1024px) {
	.className {
		margin : 0
	}
}